    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Matangi';
}

:root {
    --header-height: 5rem;
}

/* Body Section */

body {
    font-family: 'Matangi';
    font-size: 62.5%;
    min-height: 100vh;
    min-width: 100%;
    color: #fff;
    text-shadow: #000 1px 4px 4px;
}

/* Container Section */

#container {
    min-height: 100vh;
    width: 100%;
}

/* Header Section */

#header_section {
    background-color: #000;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: var(--header-height);
    padding: 0 2rem;
    opacity: .8;
}

#logo {
    font-weight: 500;
}

.weather_search_container {
    display: flex;
}

.weather_search_input {
    height: 1.85rem;
    width: 18rem;
}

.weather_search_button {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: .95rem;
    font-weight: 600;
    height: 1.85rem;
    min-width: 6rem;
    letter-spacing: .1rem;
}

/* Main Section */

#primary_weather_content {
    display: flex;
    justify-content: space-between;
    margin: 5rem 2rem 2rem 2rem;
    gap: 2.5rem;
}

#primary_second_content {
    border: 1px solid #000;
    background-color: red;
}

#primary_weather_column {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

#primary_first_content {
    display: flex;
    gap: 6rem;
}

#secondary_weather_column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
}

#current_location {
    font-size: 2.5rem;
    font-weight: 300;
    letter-spacing: .3rem;
}

#current_temperature {
    display: flex;
    font-size: 2rem;
    font-weight: 500;
    gap: .5rem;
    align-items: center;
    letter-spacing: .2rem;
}

#current_weather_conditions {
    font-size: 1.4rem;
    font-weight: 400;
    letter-spacing: .2rem;
    text-transform: capitalize;
}

#current_feels_like_temperature {
    font-size: 1.5rem;
    font-weight: 400;
    letter-spacing: .2rem;
}

#current_day_temperature_high_low, #sunrise_sunset {
    display: flex;
    flex-direction: column;
    font-size: 1.35rem;
    font-weight: 400;
    gap: 1.25rem;
    letter-spacing: .2rem;
}

#current_day_time {
    font-size: 1.5rem;
    font-weight: 400;
}

#current_humidity {
    font-size: 1.25rem;
    font-weight: 500;
    letter-spacing: .1rem;
}

#wind_speed {
    font-size: 1.25rem;
    font-weight: 500;
    letter-spacing: .1rem;
}

#uv_index {
    font-size: 1.25rem;
    font-weight: 500;
    letter-spacing: .1rem;
}

#sunrise_sunset,
#visibility {
    font-size: 1.25rem;
    font-weight: 500;
    letter-spacing: .1rem;
}

.weather_section_title {
    font-size: 1.1rem;
    font-weight: 500;
    letter-spacing: .1rem;
}

#primary_weather_hourly_forecast {
    border: 1px solid #000;
    display: flex;
    padding: 2rem 2rem 2rem 2rem;
}

#weather_hourly_container {
    display: flex;
    gap: 1rem;
}

.weather_hourly_card {
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* Footer Section */

footer {
    background-color: #000;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 0;
    min-width: 100%;
    height: 3rem;
    opacity: .8;
}

footer p {
    font-size: .85rem;
    font-weight: 600;
    text-transform: capitalize;
    letter-spacing: .1rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,sBAAsB;AAC1B;;AAEA;IACI,qBAAqB;AACzB;;AAEA,iBAAiB;;AAEjB;IACI,sBAAsB;IACtB,gBAAgB;IAChB,iBAAiB;IACjB,eAAe;IACf,WAAW;IACX,6BAA6B;AACjC;;AAEA,sBAAsB;;AAEtB;IACI,iBAAiB;IACjB,WAAW;AACf;;AAEA,mBAAmB;;AAEnB;IACI,sBAAsB;IACtB,WAAW;IACX,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,4BAA4B;IAC5B,eAAe;IACf,WAAW;AACf;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,iBAAiB;IACjB,gBAAgB;IAChB,eAAe;IACf,eAAe;IACf,qBAAqB;AACzB;;AAEA,iBAAiB;;AAEjB;IACI,aAAa;IACb,8BAA8B;IAC9B,2BAA2B;IAC3B,WAAW;AACf;;AAEA;IACI,sBAAsB;IACtB,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,gBAAgB;IAChB,UAAU;IACV,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,qBAAqB;IACrB,0BAA0B;AAC9B;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,gBAAgB;IAChB,YAAY;IACZ,qBAAqB;AACzB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;;IAEI,kBAAkB;IAClB,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,sBAAsB;IACtB,aAAa;IACb,4BAA4B;AAChC;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA,mBAAmB;;AAEnB;IACI,sBAAsB;IACtB,WAAW;IACX,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,kBAAkB;IAClB,SAAS;IACT,eAAe;IACf,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,0BAA0B;IAC1B,qBAAqB;AACzB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,200..800;1,200..800&family=Matangi:wght@300..900&family=Open+Sans:ital,wght@0,300..800;1,300.